	methods: {
		toHall(){
			uni.navigateTo({
				url:'../hall/hall'
			})
		},
		InputFocus(e) {
			this.InputBottom = e.detail.height
			console.log(this.InputBottom)

		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		search(e){
			uni.navigateTo({
				url: '../search/search'
			})
		},
		//点击弹出弹窗
		popup_bottom: function() {
			this.height = '550rpx';
			//显示
			this.show_popup();
			
		},
		//显示弹窗
		show_popup: function() {
			this.$refs.linkAddress.show();
		},
		//回掉
		confirmCallback: function() {
			let ads=this.$store.state.user_address;
			// this.address=ads.province+ads.city+ads.district;
			console.log(ads.province);
			
			switch (ads.province){
				case '北京市':
					this.address=ads.province.slice(0,-1);
					break;
				case '上海市':
					this.address=ads.province.slice(0,-1);
					break;
				case '天津市':
					this.address=ads.province.slice(0,-1);
					break;
				case '重庆市':
					this.address=ads.province.slice(0,-1);
					break;
				case '香港特别行政区':
					this.address='香港';
					break;
				case '澳门特别行政区':
					this.address='澳门';
					break;
				case '台湾':
					this.address=ads.province;
					break;
				default:
					this.address=ads.city.slice(0,-1);
					break;
			}
			
		},
		// 跳到任务详情
		taskInfo:function(e){
			uni.navigateTo({
